/* MyPortfolio.css */

.my-portfolio {
    max-width: 80%;
    margin: 0 auto;
    padding: 20px;
    background-color: #f1f1f1;
    font-family: Arial, sans-serif;
}

.portfolio-title {
    text-align: center;
    color: #333;
    font-size: 24px;
    margin-bottom: 20px;
}

.repo-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
}

